}
.container {
  max-width: 115.2rem;
  padding: 0;
}
.container.inwards {
  width: 74rem;
}
.bb {
  border-bottom: 1px solid #e6e6e6;
}
.row {
  width: 100%;
  margin-left: 0;
}
.row .column {
  padding: 0 1.6rem;
}
.page-title {
  margin-bottom: 4.8rem;
}
.hide {
  display: none;
}
.show {
  display: block;
}
.top-container > :first-child {
  background-color: #193c6d;
