}
.top-container .header nav.mobile .search i {
  margin: 0;
}
.top-container .header nav ul {
  list-style-type: none;
  display: flex;
  align-items: center;
  width: 100%;
  margin: auto;
}
.top-container .header nav ul li {
  margin: 0 1rem;
  height: 2.4rem;
}
.top-container .header nav ul li a {
  display: flex;
  align-items: center;
}
.top-container .header nav ul li a span {
  color: #000;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.4rem;
  padding-top: 0.4rem;
}
.top-container .header nav ul li a i {
  text-decoration: none;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.8rem;
}
.top-container .header nav ul li a:hover span {
  opacity: 1;
