}
.top-container .header nav ul li a:hover i {
  opacity: 1;
}
.top-container .header nav .burger {
  color: #000;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.4rem;
  width: 2rem;
  height: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.top-container .header nav .burger i {
  margin: 0;
}
.top-container .header nav .burger > :last-child {
  display: none;
}
.top-container .header nav .burger.active > :first-child {
  display: none;
}
.top-container .header nav .burger.active > :last-child {
  display: flex;
}
.top-container .header nav .burger.active > :last-child:hover {
  color: #b8181c;
}
.top-container .header .row > :last-child {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
@media screen and (max-width: 1279px) {
  .top-container > :first-child {
    padding: 1.2rem 0;
  }
  .top-container > :first-child .row {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .top-container > :first-child .row > :first-child {
    display: flex;
    align-items: center;
  }
  .top-container > :first-child .row > :first-child a {
    margin: 0 1.6rem 0 0;
  }
}
@media screen and (max-width: 980px) {
  .top-container .header nav.desktop {
    display: none;
  }
  .top-container .header nav.mobile {
    display: flex;
  }
}
@media screen and (max-width: 640px) {
  .top-container > :first-child {
    padding: 1rem 0;
  }
  .top-container > :first-child i {
    margin-right: 1rem;
  }
  .top-container > :first-child p {
    font-size: 1.6rem;
    font-weight: 400;
    line-height: 2.4rem;
  }
  .top-container > :first-child > :first-child > :first-child > :first-child {
    display: none;
  }
  .top-container > :first-child > :first-child > :first-child > :last-child {
    justify-content: center;
    flex: 0 0 100%;
    max-width: 100%;
  }
  .top-container .header .row > :last-child {
    justify-content: center;
    margin-top: 2.4rem;
  }
  .top-container .header .row > :last-child a {
    width: 100%;
  }
}
.headerMenu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style-type: none;
  position: relative;
  padding: 2.8rem 2rem;
}
.headerMenu > :last-child {
  margin-right: 0;
}
.headerMenu li {
  margin-right: 3.2rem;
  margin-bottom: 0;
  padding: 0.4rem 0;
}
.headerMenu li a {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.headerMenu li a span {
  color: #000;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.4rem;
  padding-top: 0.4rem;
}
.headerMenu li a:hover span {
  opacity: 1;
  color: #b8181c;
}
.headerMenu li a:hover i {
  opacity: 1;
}
.headerMenu li i {
  width: 2.4rem;
  height: 2.4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.6rem;
  margin-left: 0.4rem;
}
.headerMenu li .submenu {
