}
@media screen and (max-width: 980px) {
  .headerMenu {
    display: none;
  }
}
.breadcrumbs {
  position: relative;
  padding: 2.4rem 0 2.6rem;
}
.breadcrumbs .container {
  padding: 0 1.6rem;
}
.breadcrumbs ul {
  list-style-type: none;
  display: flex;
  margin: auto;
  align-items: center;
}
.breadcrumbs ul li {
  padding: 0 0.8rem;
  position: relative;
  margin: 0;
  line-height: 1;
}
.breadcrumbs ul li:before {
  content: '/';
  position: absolute;
  top: 50%;
  left: -0.203rem;
  transform: translateY(-50%);
  color: #939393;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.6rem;
}
.breadcrumbs ul > :first-child {
  padding-left: 0;
}
.breadcrumbs ul > :first-child:before {
  content: none;
}
.breadcrumbs ul > :last-child a {
  cursor: default;
  pointer-events: none;
}
.breadcrumbs a {
  color: #939393;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 1.6rem;
}
.breadcrumbs a:hover {
  color: #b8181c;
}
@media screen and (max-width: 640px) {
  .breadcrumbs {
    padding: 2rem 0 1.6rem;
  }
  .breadcrumbs .container {
    padding: 0 2rem;
  }
}
