.content {
  list-style-type: none;
}
.content ul {
  margin-left: 0;
  padding-left: 1.8rem;
}
.content_list {
  display: block;
  height: 100%;
  position: relative;
  overflow: inherit;
  padding-bottom: 2.4rem;
}
.content_list ul {
  position: sticky;
  top: 0;
  padding: 0.4rem 0;
  margin: 0;
  list-style-type: none;
  max-width: 22.2rem;
}
.content_list ul > :last-child {
  margin-bottom: 0;
}
.content_list ul li {
  margin-bottom: 2rem;
  line-height: 1;
}
.content_list ul li a {
  color: #000;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2rem;
}
.content_list ul li a.active {
  color: #b8181c;
}
.content_list ul li a:hover {
  color: #b8181c;
}
@media screen and (max-width: 680px) {
  .content_list {
    display: none;
  }
}
.content__item {
  padding-bottom: 3rem;
  margin-bottom: 2.4rem;
  position: relative;
}
.content__item .title {
  color: #000;
  font-size: 2rem;
  font-weight: 400;
  line-height: 2.8rem;
  margin-bottom: 2rem;
  pointer-events: none;
}
.content__item .title ~ i {
  display: none;
  width: 2.4rem;
  height: 2.4rem;
  line-height: 1.4rem;
  font-size: 1.4rem;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  right: 0;
  color: #313131;
}
.content__item .title ~ i.active {
  transform: rotate(45deg);
}
.content__item ul {
  list-style-type: none;
  margin-bottom: 0;
}
.content__item ul li {
  padding-left: 1.8rem;
  margin-bottom: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.content__item ul li a {
  position: relative;
  color: #313131;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.4rem;
}
.content__item ul li a:before {
  content: '';
  display: block;
  position: absolute;
  width: 0.8rem;
  height: 0.8rem;
  border-radius: 2.6rem;
  background-color: #b8181c;
  opacity: 0.3;
  left: -1.8rem;
  top: 0.6rem;
}
.content__item ul li a:hover {
  text-decoration: underline;
  color: #b8181c;
}
.content__item ul li a:hover:before {
  opacity: 1;
}
.content__item ul > :last-child {
  margin-bottom: 0;
}
@media screen and (max-width: 640px) {
  ul.content {
    border-top: 1px solid #e6e6e6;
  }
  ul.content .content__item {
    padding: 1.4rem 3.6rem 2.1rem 0;
    margin: 0;
  }
  ul.content .content__item ul {
    overflow: hidden;
    height: 0;
    margin: 0;
  }
  ul.content .content__item.show .title ~ i {
    transform: rotate(45deg);
  }
  ul.content .content__item.show ul {
    transition: 0.25s ease-in-out;
    margin-top: 1.6rem;
    height: auto;
  }
  ul.content .content__item .title {
    margin-bottom: 0;
  }
  ul.content .content__item .title ~ i {
    display: flex;
    top: 1.6rem;
  }
}
