.links__container {
  display: grid;
  grid-template-columns: 25% 75%;
  margin-top: 4rem;
  width: 100%;
}
.links__container > :first-child {
  padding-right: 1.6rem;
  padding-left: 0;
}
.links__container > :last-child {
  padding-right: 0;
  padding-left: 1.6rem;
}
.links__container > :last-child > :first-child {
  display: grid;
  grid-template-columns: 1fr 2fr;
}
.links__container > :last-child > :first-child > :first-child {
  padding-right: 1.6rem;
}
.links__container > :last-child > :first-child > :last-child {
  padding-left: 1.6rem;
}
.links__container > :last-child > :first-child > :last-child > :first-child {
  display: grid;
  grid-template-columns: calc(50% - 1.6rem) calc(50% - 1.6rem);
  grid-column-gap: 3.2rem;
  grid-row-gap: 4rem;
}
.links__container .partition li {
  line-height: 1;
  margin-bottom: 1.6rem;
  transition: 0.25s ease-in-out;
}
.links__container .partition a {
  color: #3a3a3a;
  font-size: 1.4rem;
  font-weight: 400;
  line-height: 2rem;
}
.links__container .partition a:hover {
  color: #b8181c;
}
.links__container .partition.autoHeight li {
  height: auto;
  margin-bottom: 1.62rem;
}
.links__container .partition.autoHeight > :first-child a {
  color: #b8181c;
}
.links__container .partition.autoHeight > :first-child i {
  color: #b8181c;
  transform: rotate(-90deg);
}
.links__container .partition.autoHeight > :last-child {
  margin-bottom: 0;
}
.links__container .partition > :last-child {
  margin-bottom: 0;
}
.links__container .partition > :first-child {
  margin-bottom: 2.4rem;
  display: flex;
  align-items: flex-end;
}
.links__container .partition > :first-child i {
  width: 2.4rem;
  height: 2.4rem;
  align-items: center;
  justify-content: center;
  font-size: 1.6rem;
  margin-left: 0.4rem;
  display: none;
}
.links__container .partition > :first-child a {
  color: #000;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 2.4rem;
}
.links__container .partition > :first-child a:hover {
  color: #b8181c;
}
.links__container .partition > :first-child a[href="javascript:void(0);"] {
  pointer-events: none;
}
@media screen and (max-width: 980px) {
  .links__container {
    grid-template-columns: 1fr 1fr;
  }
  .links__container .partition li {
    margin: 0px;
    overflow: hidden;
    height: 0px;
    cursor: pointer;
  }
  .links__container .partition li:hover a,
  .links__container .partition li:hover i {
    color: #b8181c;
  }
  .links__container .partition li a {
    flex: 1;
  }
  .links__container .partition > :first-child {
    height: auto;
    margin-bottom: 0;
    margin-top: 2.4rem;
    padding-right: 1.6rem;
  }
  .links__container .partition > :first-child i {
    display: flex;
  }
  .links__container > :first-child {
    padding-left: 0;
  }
  .links__container > :last-child ul > :first-child {
    padding-right: 0;
  }
  .links__container > :last-child > :first-child {
    padding-left: 2rem;
    grid-row-gap: 0;
    grid-template-columns: 1fr;
  }
  .links__container > :last-child > :first-child > :first-child {
    padding-right: 0;
  }
  .links__container > :last-child > :first-child > :first-child > :first-child > :first-child {
    margin-top: 0;
  }
  .links__container > :last-child > :first-child > :last-child {
    padding: 0;
  }
  .links__container > :last-child > :first-child > :last-child > :first-child {
    grid-row-gap: 0;
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width: 640px) {
  .links__container .partition li a {
    flex: none;
    flex: 1;
  }
  .links__container > :last-child > :first-child {
    padding-left: 0;
    display: block;
  }
  .links__container > :last-child > :first-child > :last-child > :first-child {
    display: block;
  }
}
@media screen and (max-width: 600px) {
  .links__container {
    flex-direction: column;
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    margin-top: 0;
  }
